
.sketches .container {
  padding-top: 0;
  margin-top: -30px;
}
.sketches .container.tatoo-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 100vh;
}
.sketches h2 {
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  padding-bottom: 0.75em;
}
.sketches svg path,
.sketches svg line,
.sketches svg circle {
  fill: rgba(255, 255, 255, 0);
  stroke: #fff;
  stroke-width: 1px;
  transition: stroke 0.3s ease;
}
.sketches a.inactive {
  pointer-events: none;
  opacity: .3;
}
.sketches a:hover path,
.sketches a:hover line,
.sketches a:hover circle {
  stroke: #ceb888;
}



