
.location {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.location .container {
    text-align: center;
    max-width: initial;
    color: white;
}
.location .container:before, .location .container:after {
      content: ' ';
      display: table;
      clear: both;
}
.location .map-holder {
    width: 100%;
    height: 400px;
    max-width: 400px;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    margin: auto;
}
.location .label-caption {
    margin-top: 1em;
    font-size: 36px;
    font-style: italic;
    font-weight: 100;
    line-height: 1.3em;
}
.location .gimme-drinks {
    letter-spacing: 0.075em;
    line-height: 1.3em;
    font-family: 'Crimson Text', serif;
}
.location svg {
    cursor: move;
}
.location #sphere {
    stroke: none;
    fill: none;
}
.location #paris {
    fill: #80ffe5;
    transition: opacity 1s ease;
}
.location #paris:hover {
      fill: red;
}
.location .stroke {
    fill: none;
    stroke: #000;
    stroke-width: 3px;
}
.location .fill {
    fill: #fff;
}
.location .graticule {
    fill: none;
    stroke: #fff;
    stroke-width: 0.5px;
    stroke-opacity: 0.1;
}
.location .land {
    fill: #fff;
}
.location .boundary {
    fill: none;
    stroke: #fff;
    stroke-width: 0.5px;
}



